.coverContainer {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
}

.coverContainer > h1 {
    color: white;
    font-size: 100px;
}

/*img {*/
/*    width: 300px;*/
/*}*/

.coverContainer > p {
    margin-top: 8px;
    color: white;
    font-size: 32px;
}

@media screen and (max-width: 960px) {
    .coverContainer > h1 {
        font-size: 70px;
        text-align: center;
    }

    .coverContainer > p {
        text-align: center;
        font-size: 22px;
        width: 85%;
    }
}