.footer {
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    padding-top: 180px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

.footerInfo {
    font-size: 35px;
    padding: 30px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footerInfo > h1 {
    padding-bottom: 15px;
}

.footerContact {
    font-size: 22px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footerSns {
    width: 70%;
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.snsLinks {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.linkedin,
.twitter,
.facebook i{
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}

.linkedin:hover,
.twitter:hover,
i:hover {
    border-color: white;
    transition: all 0.3s ease-out;
}

i {
    color: white;
    font-size: 35px;
}

.designBy {
    font-size: 18px;
    padding-left: 50px;
    color: white;
}

@media only screen and (max-width: 960px) {
    .footerInfo {
        text-align: center;
        padding: 0;
        font-size: 20px;
    }

    .footerContact {
        font-size: 16px;
        padding: 100px 0 30px 0;
        text-align: center;
    }

    .footerSns {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .designBy {
        padding: 0 0 40px 0;
    }
}


.registerForm{
    display: flex;
}